import React from "react"
import Layout from "./Layout";
import {SEO} from "theme-seo";
import {Link} from "gatsby"
import {GetCities, provinces} from './Iran'
import {GetBackendUrl} from "theme-library";

const Profile = ({pageContext}) => {

    let parseServerUrl = pageContext.parseServerUrl;
    const backendUrl = GetBackendUrl();
    if(backendUrl !== null)
        parseServerUrl = backendUrl

    const [finalUser, setFinalUser] = React.useState()
    const urlParams = new URLSearchParams(typeof window !== 'undefined' ? window.location.search : "");
    const returnUrl = urlParams.get("returnUrl");

    const [formISValid, setFormValidation] = React.useState(true);

    const [user, setUser] = React.useState({Name: "", FamilyName: "", CodeMelli: "", username: "", Ostan: "", Shahr: "", Moaref: "", NahveAshnaei: "", Senf: "",
        Address: "", CodePosti: "", CodeEghtesadi: "", ShenaseMelli:"", NameSherkat:""});
    const [invoices, setInvoice] = React.useState([]);
    const [states, setStates] = React.useState([]);
    const [cities, setCities] = React.useState([]);
    const [nahveAshnaei, setNahveAshnaei] = React.useState([]);
    const [asnaf, setAsnaf] = React.useState([]);
    const [isEditing, setIsEditing] = React.useState(returnUrl !== null);

    const claims = getCookie("Claims");
    const sessionToken = claims ? JSON.parse(decodeURIComponent(claims)).SessionToken : null;

    const multiCashDownloads = [{title: "   ", url: pageContext.downloadsUrl + "Setup-Local.zip"},
                                {title: "      ", url: pageContext.downloadsUrl + "Setup-Local-With-PreRequirement.zip"},
                                {title: "   ", url: pageContext.downloadsUrl + "Setup-Server.zip"},
                                {title: "      ", url: pageContext.downloadsUrl + "Setup-Server-With-PreRequirement.zip"}]

    const singleCashDownloads = [{title: "    ", url: pageContext.downloadsUrl + "Setup-Local.zip"},
                                {title: "       ", url: pageContext.downloadsUrl + "Setup-Local-With-PreRequirement.zip"}]
        React.useEffect(function () {
            if(sessionToken) {

                fetch(parseServerUrl + 'parse/Users/me', {
                        headers: {
                            'X-Parse-Session-Token': `${sessionToken}`,
                            'X-Parse-Application-Id': `${pageContext.applicationId}`
                        }
                    }
                ).then(function (results) {
                    return results.json();
                }).then(function (r) {
                    let newUser = {};
                    newUser.objectId = r.objectId;
                    newUser.Name = r.Name || ''
                    newUser.FamilyName = r.FamilyName || ''
                    newUser.CodeMelli = r.CodeMelli || ''
                    newUser.username = r.username || ''
                    newUser.Ostan = r.Ostan || ''
                    newUser.Shahr = r.Shahr || ''
                    newUser.Moaref = r.Moaref || ''
                    newUser.NahveAshnaei = r.NahveAshnaei || ''
                    newUser.Senf = r.Senf || ''
                    newUser.Address = r.Address || ''
                    newUser.NameSherkat = r.NameSherkat || ''
                    newUser.ShenaseMelli = r.ShenaseMelli || ''
                    newUser.CodePosti = r.CodePosti || ''
                    newUser.CodeEghtesadi = r.CodeEghtesadi || ''

                    setUser(newUser);
                    setFinalUser({...newUser})

                    SetAllStates(newUser);

                    SetAllNahveAshnaei();
                    SetAllAsnaf();

                }).then(function () {
                    fetch(parseServerUrl + 'parse/classes/Invoice', {
                        headers: {
                            'X-Parse-Application-Id': `${pageContext.applicationId}`,
                            'X-Parse-Session-Token': `${sessionToken}`,
                        },

                    }).then(function (response) {
                        return response.json();
                    }).then(function (result) {
                        if (result.error === "Invalid session token") {
                            window.location = pageContext.authUrl + '?returnUrl=' + window.location
                        }

                        fetch(parseServerUrl + 'parse/classes/Option', {
                              headers: {
                                  'X-Parse-Application-Id': `${pageContext.applicationId}`
                              }
                          }
                        ).then(function (response) {
                            return response.json();
                        }).then(function (r){
                            let successInvoices = result.results.filter(invoice => invoice.paymentResult !== undefined)
                              .sort((a, b) => new Date(b.Date) - new Date(a.Date))
                              .map(invoice => {
                                  if (invoice.Options) {
                                      const options = r.results.filter(option =>
                                        invoice.Options.split(",").includes(option.objectId)
                                      )
                                      invoice.EmkanatJanebi = options.map(op => op.Title)
                                  }
                                  try {
                                      invoice.ShenasehFaalSazi = JSON.parse(invoice.ShenasehFaalSazi);
                                  } catch (e) {
                                      invoice.ShenasehFaalSazi = {"-": invoice.ShenasehFaalSazi ? invoice.ShenasehFaalSazi : " "}
                                  }
                                  return invoice;
                              });
                            setInvoice(successInvoices)
                        })
                    })
                });
            }


        }, [])

    function getCookie(name) {
        if (typeof document === 'undefined')
            return;
        const value = `; ${document.cookie}`;
        const parts = value.split(`; ${name}=`);
        if (parts.length === 2) return parts.pop().split(';').shift();
    }

    function Edit() {
        setIsEditing(true)
    }
    
    function Cancel() {
        setIsEditing(false)
        setUser({...finalUser})
    }

    function Save() {
        if(returnUrl && !user.CodeMelli && !user.CodeEghtesadi){
            setFormValidation(false);
            return;
        }
        else if(returnUrl && user.CodeMelli && (user.Name === "" || user.Shahr === "" || user.Ostan === "" || user.FamilyName === "" || user.username === "" || user.Senf === "") ) {
            setFormValidation(false);
            return;
        }
        else if (returnUrl && user.CodeEghtesadi && ( user.ShenaseMelli === "" || user.Address === "" || user.CodePosti === ""))
        {
            setFormValidation(false)
            return;
        }
        else
            setFormValidation(true);

        setFinalUser({...user});

        fetch(parseServerUrl + '/parse/Users/'+ user.objectId, {
            method: 'PUT',
            headers: {
                'X-Parse-Application-Id': `${pageContext.applicationId}`,
                'X-Parse-Session-Token': `${sessionToken}`,
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(user)
        }).then(function (response) {
            return response.json();
        }).then(function (result) {
            setIsEditing(false)
            window.RefreshToken(pageContext.authUrl);
            if (returnUrl) {
                setTimeout(() => {
                    window.location = returnUrl;
                }, 4000)
            }
        })
    }

    function handleInputChange(event) {
        let value = event.target.value;
        if(event.target.name === "CodeMelli" || event.target.name === "username" ||
            event.target.name === "CodeEghtesadi" || event.target.name === "CodePosti" || event.target.name === "ShenaseMelli"){
            if (value !== '' &&  !/^[0-9\b]+$/.test(value)) {
                return
            }
        }

        const clonedUser = {...user};
        clonedUser[event.target.name] = value.toString();
        setUser(clonedUser)
    }

    function NumberWithCommas(x) {
        return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }

    function SetAllStates(user) {
        provinces.unshift({id: "*", name: ""});
        setStates(provinces)
        if (user.Ostan){
            let stateId = provinces.filter(s => s.name === user.Ostan)[0].id;
            SetRelatedCities(stateId, user.Shahr)
        }


    }

    function SetRelatedCities(id) {
        let stateId = id;
        if(!id)
        {
            let stateName = document.getElementById('states').value;

            const clonedUser = {...user};
            clonedUser["Ostan"] = stateName;
            clonedUser["Shahr"] = "";
            setUser(clonedUser)

            if(stateName)
                stateId = states.filter(s => s.name === stateName)[0].id;
        }
        if (stateId){
            let cities = GetCities(stateId);
            cities.unshift({id: "*", name: ""})
            setCities(cities);

        }
        else
        {
            setCities([]);
        }

    }

    function SetAllNahveAshnaei() {
        fetch(parseServerUrl + 'parse/classes/NahveAshnaei', {
                headers: {
                    'X-Parse-Application-Id': `${pageContext.applicationId}`
                }
            }
        ).then(function (response) {
            return response.json();
        }).then(function (response) {
            let ways = response.results.map(way => way.Title);
            ways.unshift("")
            setNahveAshnaei(ways);

        })
    }

    function SetAllAsnaf() {
        fetch(parseServerUrl + 'parse/classes/Asnaf', {
                headers: {
                    'X-Parse-Application-Id': `${pageContext.applicationId}`
                }
            }
        ).then(function (response) {
            return response.json();
        }).then(function (response) {
            let allAsnaf = response.results.map(senf => senf.Title);
            allAsnaf.unshift("")
            setAsnaf(allAsnaf);
        })
    }
    const validationHaghighi = !formISValid && user.CodeMelli && returnUrl;
    const validationHoghoghi = !formISValid && user.CodeEghtesadi && returnUrl;
    return (
        <Layout>
            <SEO title={pageContext.title}/>
            <div className="my-8 p-2 mx-auto">
                {returnUrl && <div className="my-10 text-gray-600 font-bold w-4/5 mx-auto"><label>        .</label></div>}

                <form className="text-gray-600 my-10 mx-auto w-11/12 lg:w-3/4">
                    <fieldset className="shadow-md border-gray-300 border-2 border-double p-8 pt-4 pr-4">
                        <legend className="text-xl font-bold"> </legend>
                        {!formISValid && returnUrl && (user.CodeMelli || user.CodeEghtesadi) && <label className="text-sm text-red-800">        .</label>}
                        {!formISValid && returnUrl && !user.CodeMelli && !user.CodeEghtesadi && <label className="text-sm text-red-800">                   .</label>}
                        <div className="flex flex-col lg:flex-row">
                            <div className="w-full mx-3">
                                <label className={(validationHaghighi && user.Name === "" ? "text-red-800" : "") + " block font-bold my-2 " }>
                                    <span className="font-medium">{!isEditing ? ' :  ' + user.Name : ''}</span>
                                    {validationHaghighi && user.Name === "" && <span className="text-red-800 m-1">*</span>}
                                </label>
                                {isEditing && <input
                                    className={(validationHaghighi && user.Name === "" ? "border-red-700" : "") + " shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"}
                                    value={user.Name} onChange={handleInputChange} name={"Name"}/>}
                            </div>
                            <div className="w-full mx-3">
                                <label className={(validationHaghighi && user.FamilyName === "" ? "text-red-800" : "") + " block font-bold my-2 " }> 
                                    <span className="font-medium">{!isEditing ? ' :  ' + user.FamilyName : ''}</span>
                                    {validationHaghighi && user.FamilyName === "" && <span className="text-red-800 m-1">*</span>}
                                </label>
                                {isEditing && <input
                                    className={(validationHaghighi && user.FamilyName === "" ? "border-red-700" : "") + " shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"}
                                    value={user.FamilyName} onChange={handleInputChange} name={"FamilyName"}/>}
                            </div>
                            <div className="w-full mx-3">
                                <label className={(!formISValid && user.CodeEghtesadi === "" && returnUrl && user.CodeMelli === "" ? "text-red-800" : "") + " block font-bold my-2 " }> 
                                    <span className="font-medium">{!isEditing ? ' :  ' + user.CodeMelli : ''}</span>
                                    {!formISValid && user.CodeEghtesadi === "" && returnUrl && user.CodeMelli === "" && <span className="text-red-800 m-1">*</span>}
                                </label>
                                {isEditing && <input
                                    className={(!formISValid && user.CodeEghtesadi === "" && returnUrl && user.CodeMelli === "" ? "border-red-700" : "") + " shadow appearance-none border rounded w-full py-2 px-3  leading-tight focus:outline-none focus:shadow-outline"}
                                    value={user.CodeMelli} onChange={handleInputChange} name={"CodeMelli"}/>}
                            </div>
                        </div>
                        <div className="flex flex-col lg:flex-row mt-4">

                            <div className="w-full mx-3">
                                <label className={(validationHaghighi && user.username === "" ? "text-red-800" : "") + " block font-bold my-2 " }> 
                                    <span className="font-medium">{!isEditing ? ' :  ' + user.username : ''}</span>
                                    {validationHaghighi && user.username === "" && <span className="text-red-800 m-1">*</span>}
                                </label>
                                {isEditing && <input
                                    className={(validationHaghighi && user.username === "" ? "border-red-700" : "") + " shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"}
                                    value={user.username} onChange={handleInputChange} name={"username"}/>}
                            </div>
                                <div className="w-full mx-3">
                                    <label className={(validationHaghighi && user.Ostan === ""  ? "text-red-800" : "") + " block font-bold my-2"}>
                                        <span className="font-medium">{!isEditing ? ' :  ' + user.Ostan : ''}</span>
                                        {!formISValid &&  user.CodeMelli && user.Ostan === ""  && <span className="text-red-800 m-1">*</span>}
                                    </label>
                                    {isEditing && <select id="states" name="states" onChange={() => SetRelatedCities(null, null)}
                                             value={user.Ostan}
                                             className={(validationHaghighi && user.Ostan === ""  ? "border-red-700" : "") + " w-full border shadow rounded h-10 focus:shadow-outline"}>
                                        {states.map(s =>
                                            <option className="hover:bg-teal-500" key={s.id} id={s.id}
                                                    value={s.name}>{s.name}</option>
                                        )}

                                    </select>}
                                </div>
                                <div className="w-full mx-3">
                                    <label className={(validationHaghighi && user.Shahr === ""  ? "text-red-800" : "") + " block font-bold my-2"}>
                                        <span className="font-medium">{!isEditing ? ' :  ' + user.Shahr : ''}</span>
                                        {validationHaghighi && user.Shahr === ""  && <span className="text-red-800 m-1">*</span>}
                                    </label>
                                    {isEditing && <select name="Shahr" id="cities" onChange={handleInputChange} value={user.Shahr}
                                             className={(validationHaghighi && user.Shahr === ""  ? "border-red-700" : "") + " w-full border shadow rounded h-10 focus:shadow-outline"}>
                                        {cities.length > 0 && cities.map(s =>
                                            <option className="hover:bg-teal-500" key={s.id} id={s.id}
                                                    value={s.name}>{s.name}</option>
                                        )}

                                    </select>}
                                </div>
                        </div>
                        <div className="flex flex-col lg:flex-row mt-4">

                            <div className="w-full mx-3">
                                <label className="block font-bold my-2">
                                    <span className="font-medium">{!isEditing ? ' :  ' + user.Moaref : ''}</span>
                                </label>
                                {isEditing && <input
                                    className="shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"
                                    value={user.Moaref} onChange={handleInputChange} name={"Moaref"}/>}
                            </div>
                            <div className="w-full mx-3">
                                <label className="block font-bold my-2"> 
                                    <span className="font-medium">{!isEditing ? ' :  ' + user.NahveAshnaei : ''}</span>
                                </label>
                                {isEditing && <select name="NahveAshnaei" id="NahveAshnaei" onChange={handleInputChange}
                                         value={user.NahveAshnaei}
                                         className="w-full border shadow rounded h-10 focus:shadow-outline">
                                    {nahveAshnaei.length > 0 && nahveAshnaei.map(s =>
                                        <option className="hover:bg-teal-500" key={s} id={s} value={s}>{s}</option>
                                    )}
                                </select>}
                            </div>
                            <div className="w-full mx-3">
                                <label className={((validationHoghoghi || validationHaghighi) && user.Senf === ""  ? "text-red-800" : "") + " block font-bold my-2 " }>
                                    <span className="font-medium">{!isEditing ? ' :  ' + user.Senf : ''}</span>
                                    {(validationHoghoghi || validationHaghighi) && user.Senf === ""  && <span className="text-red-800 m-1">*</span>}</label>
                                {isEditing && <select name="Senf" id="Senf" onChange={handleInputChange} value={user.Senf}
                                         className={((validationHoghoghi || validationHaghighi) && user.Senf === ""  ? "border-red-700" : "") + " w-full border shadow rounded h-10 focus:shadow-outline"}>
                                    {asnaf.length > 0 && asnaf.map(s =>
                                        <option className="hover:bg-teal-500" key={s} id={s} value={s}>{s}</option>
                                    )}
                                </select>}
                            </div>
                        </div>
                        <fieldset className="shadow-md border-gray-300 border-2 p-8 pr-6 pt-4 mt-6 ">
                            <legend className="text-xl font-bold "> </legend>
                            <div className="flex flex-col lg:flex-row">
                                <div className="w-full mx-3">
                                    <label className={(validationHoghoghi && user.NameSherkat === ""  ? "text-red-800" : "") + " block font-bold my-2 " }>  
                                        <span className="font-medium">{!isEditing ? ' :  ' + user.NameSherkat : ''}</span>
                                        {validationHoghoghi && user.NameSherkat === ""  && <span className="text-red-800 m-1">*</span>}
                                    </label>
                                    {isEditing && <input
                                        className={(validationHoghoghi && user.NameSherkat === ""  ? "border-red-700" : "") + " shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"}
                                        value={user.NameSherkat} onChange={handleInputChange} name={"NameSherkat"}/>}
                                </div>
                                <div className="w-full mx-3">
                                    <label className={(validationHoghoghi && user.ShenaseMelli === ""  ? "text-red-800" : "") + " block font-bold my-2 " }>  
                                        <span className="font-medium">{!isEditing ? ' :  ' + user.ShenaseMelli : ''}</span>
                                        {validationHoghoghi && user.ShenaseMelli === ""  && <span className="text-red-800 m-1">*</span>}
                                    </label>
                                    {isEditing && <input
                                        className={(validationHoghoghi && user.ShenaseMelli === "" ? "border-red-700" : "") + " shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"}
                                        value={user.ShenaseMelli} onChange={handleInputChange} name={"ShenaseMelli"}/>}
                                </div>
                            </div>
                            <div className="flex flex-col lg:flex-row">
                                <div className="w-full mx-3">
                                    <label className={(validationHoghoghi && user.CodePosti === ""  ? "text-red-800" : "") + " block font-bold my-2 " }> 
                                        <span className="font-medium">{!isEditing ? ' :  ' + user.CodePosti : ''}</span>
                                        {validationHoghoghi && user.CodePosti === ""  && <span className="text-red-800 m-1">*</span>}
                                    </label>
                                    {isEditing && <input
                                        className={(validationHoghoghi && user.CodePosti === ""  ? "border-red-700" : "") + " shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"}
                                        value={user.CodePosti} onChange={handleInputChange} name={"CodePosti"}/>}
                                </div>
                                <div className="w-full mx-3">
                                    <label className={(!formISValid && user.CodeMelli === "" && returnUrl && user.CodeEghtesadi === ""  ? "text-red-800" : "") + " block font-bold my-2 " }> 
                                        <span className="font-medium">{!isEditing ? ' :  ' + user.CodeEghtesadi : ''}</span>
                                        {!formISValid && user.CodeMelli === "" && returnUrl && user.CodeEghtesadi === ""  && <span className="text-red-800 m-1">*</span>}
                                    </label>
                                    {isEditing && <input
                                        className={(!formISValid && user.CodeMelli === "" && returnUrl && user.CodeEghtesadi === "" ? "border-red-700" : "") + " shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"}
                                        value={user.CodeEghtesadi} onChange={handleInputChange} name={"CodeEghtesadi"}/>}
                                </div>
                            </div>
                            <div className="flex flex-col lg:flex-row">
                                <div className="w-full mx-3">
                                    <label className={(validationHoghoghi && user.Address === ""  ? "text-red-800" : "") + " block font-bold my-2 " }>
                                        <span className="font-medium">{!isEditing ? ' :  ' + user.Address : ''}</span>
                                        {validationHoghoghi && user.Address === ""  && <span className="text-red-800 m-1">*</span>}
                                    </label>
                                    {isEditing && <input
                                        className={(validationHoghoghi && user.Address === ""  ? "border-red-700" : "") + " shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"}
                                        value={user.Address} onChange={handleInputChange} name={"Address"}/>}
                                </div>
                            </div>
                        </fieldset>
                        {!isEditing && !returnUrl && <button
                            className="bg-teal-600 hover:bg-green-600 text-white font-bold py-2 px-4 rounded-lg my-8 ml-4"
                            type="button" onClick={Edit}>  
                        </button>}
                        {isEditing && <button
                                 className="bg-teal-600 hover:bg-green-600 text-white font-bold py-2 px-4 rounded-lg my-8 ml-4"
                                 type="button" onClick={Save}>{returnUrl ? '    ' : '  '}
                        </button>}
                        {isEditing && !returnUrl && <button
                            className="bg-teal-600 hover:bg-green-600 text-white font-bold py-2 px-4 rounded-lg my-8"
                            type="button" onClick={Cancel}></button>}
                    </fieldset>

                </form>
                {invoices && invoices.length > 0 &&
                <div>
                    {
                        pageContext.relatedLinks &&
                        <div className="w-11/12 lg:w-4/5 mx-auto">
                            <div className="my-10 bg-teal-600 h-10"><p className="text-white pr-3 pt-1 text-xl">    </p></div>
                            <div className="my-6">
                                <span className="text-gray-600 text-lg my-4 pr-3">           .</span>
                            </div>
                            {pageContext.relatedLinks.map(link => link.url.startsWith("/") ?
                                <Link className="mx-4 my-4 block text-center lg:inline border bg-orange-500 text-white rounded-full py-2 px-4 shadow-lg"
                                    to={link.url}>{link.title}</Link> :
                                <a className="mx-4 my-4 block text-center lg:inline border bg-orange-500 text-white rounded-full py-2 px-4 shadow-lg"
                                   href={link.url}>{link.title}</a>)}
                        </div>
                    }
                    {
                        invoices.filter(invoice => invoice.ProductId === 'QIoToIIkS1').length > 0 &&
                        <div className="w-11/12 lg:w-4/5 mx-auto my-8">
                            <div className="my-10 bg-teal-600 h-10"><p className="text-white pr-3 pt-1 text-xl">    </p></div>
                            <div className="my-6">
                                <p className="text-gray-600 text-lg my-4 pr-3">   Net Framework 4.8.   SQL Server 2012                    . </p>
                            </div>
                            {singleCashDownloads.map(d => <a className="mx-4 my-4 block text-center lg:inline border bg-orange-500 text-white rounded-full py-2 px-4 shadow-lg"
                                                             href={d.url} >{d.title}</a>)}
                        </div>
                    }
                    {
                        invoices.filter(invoice => invoice.ProductId === 'TMtaAVSYAL').length > 0 &&
                        <div className="w-11/12 lg:w-4/5 mx-auto">
                            <div className="my-10 bg-teal-600 h-10"><p className="text-white pr-3 pt-1 text-xl">    </p></div>
                            <div className="my-6">
                                <p className="text-gray-600 text-lg my-4 pr-3">   Net Framework 4.8.   SQL Server 2012                    . </p>
                            </div>
                            {multiCashDownloads.map(d => <a className="mx-4 my-4 block text-center lg:inline border bg-orange-500 text-white rounded-full py-2 px-4 shadow-lg"
                                                            href={d.url} >{d.title}</a>)}
                        </div>
                    }
                    <div className="my-16 w-11/12 mx-auto lg:w-4/5" id="no-more-tables">
                        <div className="my-10 bg-teal-600 h-10"><p className="text-white pr-3 pt-1 text-xl">  </p></div>
                        <table className="whitespace-no-wrap text-sm lg:text-base mx-auto text-center border-collapse text-gray-700 w-full">
                            <thead>
                            <tr className="lg:py-4 lg:px-6 bg-green-500 text-white border-2 border-green-500">
                                <th> </th>
                                <th> </th>
                                <th> </th>
                                <th>   ()</th>
                                <th> </th>
                                <th> </th>
                            </tr>
                            </thead>
                            <tbody>
                            {
                                invoices.map(invoice =>(
                                    <tr key={invoice.objectId} className="whitespace-no-wrap my-8 lg:py-4 lg:px-6 border-2 lg:border-0 lg:border-b-2 border-teal-500 ">
                                        <td data-title=" " className="font-sans">{invoice.ProductName || <br/>}</td>
                                        <td data-title=" " className="font-sans">{invoice.EmkanatJanebi !== undefined && invoice.EmkanatJanebi.map(e => {return <span key={e}>{e}<br/></span>}) || <span>----------</span>}</td>
                                        <td data-title=" " className="font-sans">{invoice.Tarikh || <br/>}</td>
                                        <td data-title="   ()" className="font-sans">{invoice.paymentResult === 'OK' ? NumberWithCommas(invoice.MablaghTaeedShode/10) : ' '}</td>
                                        <td data-title=" " className="font-sans">{invoice.ShomarehPeygiri || <br/>}</td>
                                        <td data-title=" " className="font-sans">{invoice.ShenasehFaalSazi && <>
                                            {
                                                Object.keys(invoice.ShenasehFaalSazi).map((key,i) => {
                                                    return <span key={i}>{ key === '-' ? '' : key + ": "}{invoice.ShenasehFaalSazi[key]}<br/></span>
                                                })
                                            }
                                        </> || <br/>}
                                        </td>
                                    </tr>
                                ) )
                            }

                            </tbody>
                        </table>
                    </div>
                </div>
                }
            </div>
        </Layout>
    );
}

export default Profile;

